@use "src/vars";

.heading-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	margin: 0 0 50px 0;
}
.screen-sub-heading {
	letter-spacing: 3px;
	margin: 8px 0 10px 0;
	font-size: 12px;
	color: vars.$black;
}
.screen-heading {
	font-size: 32px;
	color: #1f2235;
	font-family: "Poppins Bold";
}
.heading-separator {
	display: flex;
	align-items: center;
	position: relative;
	width: 180px;
	margin: 10px 0 0 0;
}
.separator-line {
	width: 100%;
	height: 2px;
	background-colaor: #1f221f;
}
.separator-blob {
	height: 10px;
	width: 100%;
	position: absolute;
	display: flex;
	justify-content: center;
}
.separator-blob div {
	width: 35px;
	border-radius: 10px;
	background-color: vars.$title-color;
}
