@use "src/vars";

.header-container {
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	justify-content: center;
	height: 60px;
	width: 100%;
	color: vars.$white;
	background-color: vars.$background-color;
	z-index: 20;
}
.header-parent {
	display: flex;
	align-items: center;
	justify-content: space-around;
	height: 100%;
	width: 70%;
	color: vars.$white;
}
.header-logo {
	font-size: 40px;
	font-family: "Poppins ExtraBold";
	color: vars.$white;
}
.header-options {
	display: flex;
	align-items: center;
}
.header-option {
	font-size: 16px;
	cursor: pointer;
	font-weight: 600;
}
.header-hamburger {
	display: none;
	color: vars.$white;
}
.header-option-separator {
	margin: 0 30px 0 0;
	color: vars.$white;
}
a {
	text-decoration: none;
	color: inherit;
}
.selected-header-option {
	color: vars.$title-color;
}
.selected-logo-option {
	color: vars.$title-color;
}

/*IPAD PRO RESPONSIVENESS*/
@media only screen and (max-width: 1110px) {
	header-parent {
		width: 80%;
	}
}
@media only screen and (max-width: 970px) {
	.header-hamburger {
		display: block;
	}
	.header-parent {
		width: 100%;
		padding: 0 40px;
		position: relative;
	}
	.header-options {
		display: flex;
		flex-direction: column;
		position: absolute;
		left: -100%;
		transition: all 0.8s ease-out;
		top: 0px;
		height: 100vh;
		width: 100%;
		justify-content: space-around;
		background-color: #1f2235;
		z-index: 1000;
		opacity: 0.5;
	}
	.header-option-separator {
		margin: 0;
	}
	.selected-header-option {
		color: vars.$white;
	}
	.header-hamburger-bars {
		font-size: 36px;
		cursor: pointer;
		margin: 0px 10px;
		color: vars.$white;
	}
	.show-hamburger-options {
		left: 0;
		opacity: 1;
	}
}
@media only screen and (max-width: 375px) {
	.header-parent {
		margin-right: 60px;
	}
}
